.navigation__toogle {
  display: block;
  background-color: transparent;
  width: 50px;
  height: 53px;
  padding: 26px;
  border: none;
  position: relative;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__toogle {
    display: none;
  }
}

.navigation--close .navigation__toogle::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 13px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  cursor: pointer;
}

.navigation--open .navigation__toogle::before, .navigation--open .navigation__toogle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 13px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.navigation--open .navigation__toogle::before {
  transform: rotate(45deg);
}
.navigation--open .navigation__toogle::after {
  transform: rotate(-45deg);
}

